.trouble_link {
  float: left;
  text-decoration: underline;
  margin: 0 0 0 30px;
}
#main_title {
  line-height: 65px;
  font-size: 170%;
  margin-right: auto;
  margin-top: 0;
}
#loging {
  background: #F6FBFF;
  display: block;
  width: 50%;
  margin: auto;
}
#login-form {
  background: #F6FBFF;
}
#login-form input{
  margin: auto;
}
#login-form input.in_data {
  outline: none;
  width: 90%;
  border: 1px solid #c6c9cc;
  border-radius: 15px;
  color: #555;
  display: block;
  padding: 1% 2%;
  height: 50px;
  margin-top: 10px;
  margin-bottom: 10px;
}
#login-form input.error_in_data {
  outline: none;
  width: 90%;
  border: 3px solid #f30404;
  border-radius: 15px;
  color: #f30303;
  display: block;
  padding: 1% 2%;
  height: 50px;
  margin-top: 10px;
  margin-bottom: 10px;
}
#login-form .selector {
  margin-top: 20px;
  margin-right: 25px;
  margin-left: 10px;
}
#login-form label.selector_label{
  margin-left: 20px;
  color: #3e606f;
  font-family: Satisfy, Lato, Open Sans, Roboto, sans-serif;
  text-transform: capitalize;
}
#login-form label {
  color: #3e606f;
  font-family: Satisfy, Lato, Open Sans, Roboto, sans-serif;
  text-transform: capitalize;
  font-size: 170%;
}
#login-form fieldset#back {
  text-align: left;
}
#login-form fieldset {
  padding: 4%;
  border: 1px solid #F6FBFF;
  border-radius: 5px;
  margin: 10% 10%;
  text-align: center;
  margin-top: 0px;
  margin-bottom: 0px;
}
.err_message {
  font-size: 100%;
  font-style: italic;
  margin-left: auto;
  color: #cd1a21
}
.loging__btn {
  outline: 0 none !important;
  float: right;
  width: 95px;
  height: 46px;
  margin: 10px 25px 0px 0px;
  background: #F3F3F3;
  border: 2px solid #6FB2E6;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 30px;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
}
.loging__btn:hover {
  outline: none;
  background: #6FB2E6;
  color: #fff;
  -webkit-box-shadow: inset 0 0 0 3px #6FB2E6;
          box-shadow: inset 0 0 0 3px #6FB2E6;
  cursor: pointer;
}
#arrow {
  height: 95%;
  width: 95%;
}
.loging__btn:hover svg {
  fill: white;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
}
@media only all and (min-width: 882px) and (max-width: 1255px) {
#login-form {
    display: block;
    height: 100%;
    width: 100%;
}
#login-form label {
    font-size: 160%;
}
#loging {
    width: 50%;
    margin-top: 0px;
    margin-bottom: 0px;
}
}
@media only all and (max-width: 881px) {
#loging {
    display: block;
    width: 90%;
}
}

